.entireCard-appointment{
 
  background-color: whitesmoke;
  margin-bottom: 20px;
  display: grid;
  width: 800px;
  height: 190px;
  overflow: hidden;
  border: 2px solid #a9a9a9;
  border-radius: 20px;
  grid-template: 
    "img  .     "40%
    "img addr  "20%
    "img star  "20%
    "img endt  "20%
    / 40% 60% ;


}

.cardview-image-appointment{
  justify-self: left;
  grid-area: img;
  max-width: 100%;
  max-height: 100%;
  height: 190px;
}

.appointment-address{
  grid-area: addr;

}

.start-time-appointment{
  grid-area: star;
}

.end-time-appointment{
grid-area: endt;

}